import Sample1 from './src/sample1.jpg'
import Sample2 from './src/sample2.jpg'

const DateFormat = { day: 'numeric' }
const MonthFormat = { month: 'short' }
const YrFormat = { year: 'numeric' }
const WDayFormat = { weekday: 'short' }

const Poster = ({
  title,
  subTitle,
  speakerName1,
  speakerInfo1,
  speakerName2,
  speakerInfo2,
  hostName,
  hostInfo,
  abtTalk,
  date,
  startHour,
  startMinute,
  endHour,
  endMinute,
  place,
  organizer,
  coOrganizer,
  url,
  picture1,
  picture2,
}) => (
  <Frame>
    <Pic1>
      {picture1 ? (
        <Pic1Pos>
          <img src={picture1.value} alt="Error, please upload again." />
        </Pic1Pos>
      ) : (
        <Pic1Pos>
          <img src={Sample1} alt="Sample1" />
        </Pic1Pos>
      )}
    </Pic1>
    <Pic2>
      {picture2 ? (
        <Pic2Pos>
          <img src={picture2.value} alt="Error, please upload again." />
        </Pic2Pos>
      ) : (
        <Pic2Pos>
          <img src={Sample2} alt="Sample2" />
        </Pic2Pos>
      )}
    </Pic2>
    <BorderGrid>
      <FrameGrid />
    </BorderGrid>
    <BorderSplitLeft>
      <FrameSplitLeft />
    </BorderSplitLeft>
    <BorderSplitRight>
      <FrameSplitRight />
    </BorderSplitRight>
    <BorderSplitRen>
      <FrameSplitRen />
    </BorderSplitRen>
    <BorderSplitTopBg>
      <FrameSplitTop />
    </BorderSplitTopBg>
    <BorderSplitTop>
      <FrameSplitTop />
    </BorderSplitTop>
    <BorderNtuCSoc>
      <FrameNtuCSoc />
    </BorderNtuCSoc>
    <Info>
      <BoxTitle>
        <Title>{title ? title : '輸入演講標題'}</Title>
        <SubTitle>{subTitle ? subTitle : ''}</SubTitle>
      </BoxTitle>
      {speakerName2 ? (
        <>
          <BoxTwoSpeaker>
            <Caption Dark>SPEAKER</Caption>
            <Speaker>{speakerName1 ? speakerName1 : '講者 名稱'}</Speaker>
            <SpeakerInfo>
              {speakerInfo1 ? speakerInfo1 : '講者任職單位 職稱'}
            </SpeakerInfo>
            <Speaker>{speakerName2 ? speakerName2 : '講者 名稱'}</Speaker>
            <SpeakerInfo>
              {speakerInfo2 ? speakerInfo2 : '講者任職單位 職稱'}
            </SpeakerInfo>
            {hostName ? (
              <>
                <Caption>MODERATOR</Caption>
                <Host>{hostName}</Host>
                <HostInfo>{hostInfo}</HostInfo>
              </>
            ) : (
              <></>
            )}
          </BoxTwoSpeaker>
        </>
      ) : (
        <>
          <BoxOneSpeaker>
            <Caption Dark>SPEAKER</Caption>
            <Speaker>{speakerName1 ? speakerName1 : '講者 名稱'}</Speaker>
            <SpeakerInfo>
              {speakerInfo1 ? speakerInfo1 : '講者任職單位 職稱'}
            </SpeakerInfo>
            {hostName ? (
              <>
                <Caption Dark>MODERATOR</Caption>
                <Host>{hostName}</Host>
                <HostInfo>{hostInfo}</HostInfo>
              </>
            ) : (
              <></>
            )}
          </BoxOneSpeaker>
        </>
      )}
      <BoxTimePlace>
        <BoxDate>
          <Date>
            {date ? <>{date.toLocaleDateString('en', MonthFormat)}</> : <>MM</>}
            <small>. </small>
            {date ? <>{date.toLocaleDateString('en', DateFormat)}</> : <>DD</>}
          </Date>
          <YrWday>
            {date ? (
              <>{date.toLocaleDateString('en', WDayFormat)}.</>
            ) : (
              <>Day</>
            )}
            <br />
            {date ? (
              <small>{date.toLocaleDateString('en', YrFormat)}</small>
            ) : (
              <small>Year</small>
            )}
          </YrWday>
        </BoxDate>
        <Time>
          {startHour ? startHour.value : '00'}:
          {startMinute ? startMinute.value : '00'} -{' '}
          {endHour ? endHour.value : '00'}:{endMinute ? endMinute.value : '00'}
        </Time>
        <Place>{place ? place : 'Place'}</Place>
      </BoxTimePlace>
      <BoxAbt>
        <Abt_P>
          {abtTalk
            ? abtTalk
            : '請填入內容以取代此示例文字：代謝運動由一批年輕的日本建築師及設計師聯手推行，黑川紀章設計的中銀膠囊塔是其中代表。建築由一些可拆換的模塊組成，逐漸拆換模塊在多年後也許整棟建築都由新模塊組成，而達成一種動態更新。'}
        </Abt_P>
      </BoxAbt>
      <BoxOrgSign>
        <ItemOrg>
          <Caption>ORGANIZER</Caption>
          <Org>{organizer ? organizer : '國立臺灣大學社會科學院'}</Org>
          <Org>
            {coOrganizer ? (
              <>
                <Caption>CO-ORGANIZER</Caption>
                {coOrganizer}
              </>
            ) : (
              <></>
            )}
          </Org>
        </ItemOrg>
        <ItemOrg>
          <Caption>SIGN UP</Caption>
          <QR>
            {url ? (
              <QRCode
                value={url}
                bgColor="#FFEFE0"
                fgColor="#4A364E"
                includeMargin="true"
              />
            ) : (
              <QRCode
                value="https://www.google.com"
                bgColor="#FFEFE0"
                fgColor="#4A364E"
                includeMargin="true"
              />
            )}
          </QR>
        </ItemOrg>
      </BoxOrgSign>
    </Info>
  </Frame>
)

export default Poster

